import { AppError } from "../../shared/error/error";
import { ErrorCode } from "../../shared/error/error_code";
import { isOptionSymbol, onChainSymbols } from "../config";
import { pricesCache, volatilitiesCache } from "../utils/oracle";

export const sendTxWithOracleSignature = async (
  chainId,
  symbol,
  symbols,
  fn,
  args
) => {
  if (isOptionSymbol(symbol)) {
    // const newArgs = [...args, await getSignedVolatilities(chainId, symbols)];
    const newArgs = [...args, await volatilitiesCache.get(symbols)];
    return await fn(...newArgs);
  } else {
    if (onChainSymbols.includes(symbol.symbol)) {
      return await fn(...args);
    } else {
      // const newArgs = [...args, await getSignedPrices(chainId, symbols)];
      const newArgs = [...args, await pricesCache.get(symbols)];
      return await fn(...newArgs);
    }
  }
};
