const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export const asyncCache = (fn, fnName, defaultValue) => {
  let cache = {};
  let pending = {};
  return async function (...args) {
    // first arg is the cache key
    const key = `${fnName}_${args[0]}`;
    if (
      !Object.keys(cache).includes(key) ||
      Math.floor(Date.now() / 1000) - cache[key].timestamp > 60
    ) {
      let res = defaultValue;
      try {
        if (Object.keys(pending).includes(key)) {
          let retry = 10;
          while (retry > 0) {
            await delay(490);
            if (!Object.keys(pending).includes(key)) {
              //console.log('hit pending with cache');
              return cache[key].data;
            }
          }
          if (retry === 0) {
            res = await fn(...args);
            const timestamp = Math.floor(Date.now() / 1000);
            cache[key] = { data: res, timestamp };
            return res;
          }
        } else {
          pending[key] = true;
          res = await fn(...args);
          const timestamp = Math.floor(Date.now() / 1000);
          cache[key] = { data: res, timestamp };
          return res;
        }
      } catch (err) {
        if (cache[key]) {
          // return old data
          return cache[key].data;
        } else {
          // return default value
          return res;
        }
      } finally {
        delete pending[key];
      }
    } else {
      // return the cached data
      return cache[key].data;
    }
  };
};
