import { stringToId } from "./misc.js";
import { oracleManagerFactory } from "../contract/factory/rest.js";
import { DEBUG } from "../../shared/config/constant.js";
import { normalizeOptionSymbol, getNormalizedOptionSymbols, getVolatilitySymbol } from "./config.js";
import { DeriEnv } from "../../shared/config/env";

export const PRESERVED_SYMBOLS = ["BTCUSD", "ETHUSD", "BNBUSD", "NULSUSDT", "ONTUSD"];

const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms))

export const getOracleServerUrl = (symbol, type = "future", version="") => {
  //if (/^[0-9]+$/.test(symbolId.toString())) {
  let method = version === "v3" ? "get_signed_price_v3" : "get_signed_price" 
  if (type === "option") {
    if (version === "v3") {
      method = "get_signed_volatility_v3";
    } else {
      method = "get_signed_volatility";
    }
  }
  if (version !== 'v3' && symbol && PRESERVED_SYMBOLS.includes(symbol.toUpperCase())) {
    method = "get_price";
    symbol = `${symbol}_v2_bsc`;
  }
  let baseUrl = DeriEnv.get() === 'prod'
    ? `https://oraclemainnet.deri.finance/${method}`
    : `https://oracletestnet.deri.finance/${method}`;
  const addSymbolParam = (url, symbol) => `${url}?symbol=${symbol}`;

  if (symbol) {
    return addSymbolParam(baseUrl, symbol);
  } else {
    return baseUrl;
  }
};

export const getPriceInfoFromRest = async (symbol, type='future', version='') => {
  let url = getOracleServerUrl(symbol, type, version);
  let retry = 3;
  DEBUG && console.log('symbol oracle url: ', url)
  let res, priceInfo;
  while (retry > 0) {
    try {
      res = await fetch(url, {
        mode: "cors",
        cache: "no-cache",
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        //body: JSON.stringify(symbolList),
      });
      if (res.ok) {
        priceInfo = await res.json();
        if (priceInfo.status.toString() === "200" && priceInfo.data) {
          return priceInfo.data;
        }
      }
    } catch (err) {
      DEBUG && console.log(err)
    }
    retry -= 1;
  }
  if (retry === 0) {
    throw new Error(
      `getPriceInfoFromRest exceed max retry(3): ${symbol}`
    );
  }
};
export const getOracleInfosFromRest = async (symbolList, type = 'future', version='') => {
  //let url = getOracleUrl(null, type);
  let url = getOracleServerUrl(null, type, version);
  let retry = 2;
  let res, priceInfo;
  while (retry > 0) {
    try {
      res = await fetch(url, {
        mode: "cors",
        cache: "no-cache",
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(symbolList),
      });
      if (res.ok) {
        priceInfo = await res.json();
        if (priceInfo.status.toString() === "200" && priceInfo.data) {
          return priceInfo.data;
        }
      }
    } catch (err) {
      DEBUG &&  console.log(err)
    }
    retry -= 1;
  }
  if (retry === 0) {
    throw new Error('REST_API_CALL_TIMEOUT', url);
  }
};

export const getSymbolPrice = async (chainId, oracleManagerAddress, symbol, version) => {
  if (oracleManagerAddress !== '') {
  const id = symbol.length === 66 ? symbol : stringToId(symbol)
  const oracleManager = oracleManagerFactory(chainId, oracleManagerAddress);
  return await oracleManager.getValue(id);
  } else {
    const priceInfo = await getPriceInfoFromRest(symbol, version);
    return priceInfo.price
  }
};

export const getSymbolVolatility = async (symbol, version = "") => {
  const info = await getPriceInfoFromRest(symbol, 'option', version);
  if (info.volatility) {
    return info.volatility;
  }
  throw new Error('invalid data type: symbol volatility is empty')
};

export const getSymbolVolatilitiesForLens = async (
  chainId,
  symbols = [],
  version = ""
) => {
  const volatilities = await Promise.all(
    symbols.reduce(
      (acc, s) => [...acc, getSymbolVolatility(getVolatilitySymbol(s), version)],
      []
    )
  );
  return symbols.map((s, index) => [s, "0", volatilities[index]]);
};

export const getSymbolVolatilityInfo = async (symbol, version = "") => {
  const info = await getPriceInfoFromRest(
    symbol,
    "option",
    version
  );
  if (info.volatility) {
    return info;
  }
  throw new Error('invalid data type: symbol volatility is empty')
};


export const getSignedVolatilities = async (chainId, symbols = []) => {
  let volatilities = [];
  let oracleSymbols = [];
  // const volatilityInfos = await Promise.all(
  //   getNormalizedOptionSymbols(symbols).reduce(
  //     (acc, s) => [...acc, getSymbolVolatilityInfo(`VOL-${s}`, "v3")],
  //     []
  //   )
  // );
  let volatilityInfos = await getOracleInfosFromRest(getNormalizedOptionSymbols(symbols).map((s) => getVolatilitySymbol(s)), 'option', 'v3')
  volatilityInfos = Object.values(volatilityInfos)
  volatilities = Object.values(symbols).reduce((acc, s) => {
    const oracleSymbol = normalizeOptionSymbol(s);
    const p = volatilityInfos.find((vol) => vol.symbolName === getVolatilitySymbol(oracleSymbol))
    if (!oracleSymbols.includes(oracleSymbol) && p) {
      oracleSymbols.push(oracleSymbol);
      acc.push([
        stringToId(getVolatilitySymbol(oracleSymbol)),
        p.timestamp,
        p.volatility,
        parseInt(p.v).toString(),
        p.r,
        p.s,
      ]);
    }
    return acc;
  }, []);
  return volatilities;
};

export const getSignedPrices = async (chainId, symbols = []) => {
  let res = [];
  if (symbols.length > 0) {
    // const priceInfos = await Promise.all(
    //   symbols.reduce((acc, s) => [
    //     ...acc,
    //     getPriceInfoFromRest(s, "future", "v3"),
    //   ], [])
    // );
    const priceInfos = await getOracleInfosFromRest(symbols, 'future', 'v3')
    const prices = Object.values(priceInfos)
    res = symbols.reduce((acc, s) => {
      const price = prices.find((p) => p.symbolName === s)
      acc.push([
        stringToId(s),
        price.timestamp,
        price.price,
        parseInt(price.v).toString(),
        price.r,
        price.s,
      ]);
      return acc;
    }, []);
  }
  return res;
};

export const volatilitiesCache = (function() {
  const cache = {}
  const pending = {}
  return {
    async get(symbols=['BTCUSD']){
      const key = symbols.join('_')
      if (
        !Object.keys(cache).includes(key) ||
        Math.floor(Date.now() / 1000) - cache[key].timestamp > 10
      ) {
        const timestamp = Math.floor(Date.now() / 1000);
        // pending is exit
        if (Object.keys(pending).includes(key)) {
          let retry = 10;
          while (retry > 0) {
            await delay(270);
            if (!Object.keys(pending).includes(key)) {
              //console.log('hit pending with cache');
              return cache[key].data;
            }
          }
          if (retry === 0) {
            //console.log('hit pending expired');
            const data = await getSignedVolatilities('', symbols);
            cache[key] = {
              data,
              timestamp,
            };
            return cache[key].data;
          }
        } else {
          pending[key] = true;
          try {
            //console.log('hit new');
            const data = await getSignedVolatilities('', symbols);
            //console.log('data', data)
            cache[key] = {
              data,
              timestamp,
            };
            return cache[key].data;
          } catch (err) {
            DEBUG && console.log(err)
          } finally {
            delete pending[key];
          }
        }
      } else {
        //console.log('hit cache');
        return cache[key].data;
      }
    }
  }
})()

